
import React from 'react';
import './nav_bar.css'

import './pages/order.js'
import { order as order_0 } from './pages/order.js'; 

var debug = 1; 

export async function NavigationBar(props) {

    let nav_bar_tabs = props.nav_bar_tabs; 
    let nav_bar_tab_select = props.nav_bar_tab_select; 
    let set_active_tab = props.set_active_tab; 

    return (

    <div className="nav-bar">

        <a href="https://innowings.engg.hku.hk/">
            <img
                src="assets/images/favicon_innowing_knowledge.png"
                alt="HKU Innovation Wing"
                className="nav-bar-logo"
            />
        </a>

        <div className="nav-bar-tabs">

            {nav_bar_tabs.map(async (tab, tab_id) => {

            let current_tab = tab[0];
            // console.log(current_tab);
            let is_active_tab = nav_bar_tab_select === tab_id; 
            let tab_class = is_active_tab ? 'nav-bar-tab active' : 'nav-bar-tab'; 

            return (
                <div 
                    key={`tab-button-${tab_id}`} 
                    className={tab_class}
                    onClick = {() => set_active_tab(tab_id)}
                >
                    {current_tab}
                </div>
            );

            })}
            
        </div>

    </div>

    );

}

async function sorted_tabs(tabs, path) {

    let array_sorted = []; 

    import(`${path}/order.js`)
    .then ((module) => {
        let tabs_order = module.order
        if (debug === 1) {
            console.log (`[sorted_tabs] Input tab is: ` + tabs); 
            console.log (`[sorted_tabs] Order for the path ${path}/order.js: ` + tabs_order); 
        }
        if (!tabs_order) {
            if (debug === 1) {console.log ("order not provided; return the original array: " + tabs)}
            return tabs; 
        }
        let tabs_key = []
        let ordered_part = []
        let ordered_id = []
        let unordered_part = []
        for (let i = 0; i < tabs.length; i++) {
            typeof(tabs[i]) === `object` ? tabs_key.push(Array.from(tabs[i])[0]) : tabs_key.push(tabs[i]); 
        }
        for (let i = 0; i < tabs_order.length; i++) {
            let order_key = tabs_order[i]; 
            if (tabs_key.includes(order_key)) {
                ordered_part.push(tabs[tabs_key.indexOf(order_key)]); 
                ordered_id.push(i); 
            }
        }
        for (let i = 0; i < tabs.length; i++) {
            if (!ordered_id.includes(i)) {
                unordered_part.push(tabs[i]); 
            }
        }
        console.log("[sorted_tabs] ordered_part: " + ordered_part)
        console.log("[sorted_tabs] unordered_part: " + unordered_part)
        ordered_part.forEach((element) => {
            array_sorted.push(element); 
            console.log("[sorted_tabs] array_sorted: " + array_sorted)
        }); 
        unordered_part.forEach((element) => {
            array_sorted.push(element); 
            console.log("[sorted_tabs] array_sorted: " + array_sorted)
        }); 
        if (debug === 1) {console.log ("[sorted_tabs] returned sorted array is: " + array_sorted); }
        return (array_sorted); 
    })
    .catch ((error) => {
        if (debug === 1) {console.log (error); }
    })

    return (array_sorted); 

}

const generate_nav_bar_tabs = async (current_browse_location) => {

    const page_files = (require.context('./pages', true, /^.*\.js$/)).keys().filter((key) => key.startsWith(current_browse_location))

    if (debug === 0) {console.log(page_files); }

    var nav_bar_tabs = []; 
    var nav_bar_tabs_sorted = []; 
    let first_level_tabs = []; 

    page_files.forEach((key) => {

        if (debug === 0) {console.log(key); }
        const file_path = key.replace(current_browse_location, ''); 
        const file_path_levels = file_path.split('/')


        if (file_path_levels.length < 2) {throw new Error ("Page Parsing Error. Current directory: " + current_browse_location)}
        const first_level = file_path_levels[1]
        const second_level = file_path_levels[2]

        if (!first_level_tabs.includes(first_level) && !first_level.endsWith('.js')) {
            nav_bar_tabs[nav_bar_tabs.length] = [first_level, [second_level]]; 
            first_level_tabs.push(first_level); 
        } else {
            if (first_level_tabs.includes(first_level)) {
                console.log (nav_bar_tabs); 
                console.log (first_level); 
                console.log (nav_bar_tabs[first_level_tabs.indexOf(first_level)]); 
                if (!nav_bar_tabs[first_level_tabs.indexOf(first_level)][1].includes(second_level) && !second_level.endsWith('.js')) {
                    console.log ("first level: [" + first_level + "], pushes second level [" + second_level + "]")
                    nav_bar_tabs[first_level_tabs.indexOf(first_level)][1].push(second_level); 
                }
            }
        }

    }); 

    if (debug === 0) {console.log (`\nFirst Order Path: ./pages${current_browse_location}`); }
    if (debug === 1) {console.log("nav_bar_tabs: " + nav_bar_tabs); }
    nav_bar_tabs_sorted = await sorted_tabs(nav_bar_tabs, `./pages${current_browse_location}`); 
    if (debug === 1) {console.log("nav_bar_tabs sorted: " + nav_bar_tabs_sorted); }

    return (nav_bar_tabs_sorted); 

}

export default generate_nav_bar_tabs; 

